.sidebar {
    position: fixed;
    top: 0;
    height: 100%;
    border-right: 2px solid black;
    z-index: 1;
    background-color: white;
    padding-left: 4px;
    padding-right: 4px;
    display: grid;
    grid-template-rows: auto auto auto auto;
  }
  
  .contents {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    max-width: 321px;
  }
  
  .contents2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-y: auto;
  }
  
  .row-contents {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    height: 50px;
    flex: 1 1 0;
  }
  
  .row-contents2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 50px;
    flex: 1 1 0;
  }
  
  .right-margin {
    margin-right: 5px;
  }
  
  .left-margin {
    margin-left: 5px;
  }
  
  input[type="file"] {
    display: none;
  }
  
  #custom-button {
    display: none;
    cursor: pointer;
  }
  
  .custom-file-upload {
    /* display: inline-block; */
    cursor: pointer;
    background-image: linear-gradient(to bottom, #e4e4e4, #f7f7f7);
    border: 2px solid #4d4949;
    height: 30px;
    padding: 3px;
    align-items: center;
    /* color: disa; */
  }
  .custom-file-upload:hover{
    border: 2px solid #888;
    background-color: #ddd;
  }
  
