  .closemenu {
    color: #000;
    position: absolute;
    right: -25px;
    z-index: 9999;
    line-height: 20px;
    height: 3%;
    width: 7%;
    font-weight: bold;
    font-size: 22px;
    top: 40%;
    cursor: pointer;
  }
  
  .closemenu:hover{
    border: 2px solid #888;
